  computed:  {
    getQuizzerName: {
      get() {
        let defendant = this.$store.state.defendant_item.map(e => (e.defendant_short == '' ? e.defendant : e.defendant_short)+"").filter(i => i && i!= "")
        let accuser = this.$store.state.plaintiff_item.map(e => (e.accuser_short == '' ? e.accuser : e.accuser_short)+"").filter(i => i && i!= "")
        let thirdparty = this.$store.state.third_party_item.map(e => (e.third_party_short == '' ? e.third_party : e.third_party_short)+"").filter(i => i && i!= "")
        let judge = this.$store.state.judge_name.filter(i => i && i.trim()).map(e => e+"")
        return judge.concat(accuser).concat(defendant).concat(thirdparty)
      }
    },
    getResponderName: {
      get() {
        let defendant = this.$store.state.defendant_item.map(e => (e.defendant_short == '' ? e.defendant : e.defendant_short)+"").filter(i => i && i!= "")
        let accuser = this.$store.state.plaintiff_item.map(e => (e.accuser_short == '' ? e.accuser : e.accuser_short)+"").filter(i => i && i!= "")
        let thirdparty = this.$store.state.third_party_item.map(e => (e.third_party_short == '' ? e.third_party : e.third_party_short)+"").filter(i => i && i!= "")
        return ["","",""].concat(accuser).concat(defendant).concat(thirdparty)
      }
    },
    getDefendantThirdPartyName: {
      get() {
        let defendant = this.$store.state.defendant_item.map(e => e.defendant_short == '' ? e.defendant : e.defendant_short).filter(i => i && i.trim())
        let third_party = this.$store.state.third_party_item.filter(i=> i.third_party && i.third_party.trim()).map(function (e) {
          return (e.third_party_short==''?e.third_party:e.third_party_short) + '' ;})
        let defendant_thirdParty=defendant.concat(third_party)
        return [{defendant: "", defendant_name: defendant_thirdParty},]
      }
    },
    getAccuserName: {
      get() {
        let accuser = this.$store.state.plaintiff_item.map(e => e.accuser_short == '' ? e.accuser : e.accuser_short).filter(i => i && i.trim())
        return [{accuser: "", accuser_name: accuser}]
      }
    },
    getCounterDefendantName: {
      get() {
        let accuser = this.$store.state.plaintiff_item.map(e => e.accuser_short == '' ? e.accuser : e.accuser_short).filter(i => i && i.trim())
