    getProofOfCounterDefendant: {
      get() {
        let option_label = this.data.counterclaim_defendant_evidence.filter(i => i.evidence).map(e => {
          return "" + this.numberToChinese(e.serial) + "  " + e.evidence;
        })

        return [{evidence: "", option_label: option_label}]
      },
    },
