      accuser_evidence: [{
        serial: 1,//
        evidence: "", //( )
        content: "", //( )
        evidence_type: ""//
      }],
      // accuser_evidence_fact_reason: "", //()
      defendant_and_other_accuser_query: [
        {
          name: [], //
          evidence: [], //
          fact_reason: "", //()
          facticity: "1", //
          legality: "1", //
          relevance: "1", //
        }
      ],


      // "serial":"",
      // "evidence_type":"",
      // "evidence": "",
      // "content": "",
      // "witness_name":"",
      // "witness_type":" //",
      // "witness_testimony":[{
      //   "quizzer":"///",
      //   "question":"",
      //   "responder":"/////",
      //   "answer":""
      // }]
      accuser_evidence_witness: [
        {
          serial: 1,//
          evidence: "",
          witness_name: "",
          witness_type: "",
          content: "",
          witness_testimony: [{
            quizzer: "",
            question: "",
            responder: "",
            answer: ""
          }]
        }
      ],
      question_list: [
        " ///",
        "//",
        " / ",
        "",
        "",
        "",
        "",
        " /  / ",
        "",
        " /  /  / ",
        " /  /  / ",
        " / ",
        " / ",
        " /  / ",
        " /  / "
      ],
      witness_type: ["", "", ""],
      is_defendant_evidence:"2",
      accuser_is_witness:"1",
      judge_defendant_and_other_accuser_query:"",
      judge_accuser_evidence:"",
  }
    var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
    if (wholeItem != null && "accuserShowInfo" in wholeItem) {
      data = wholeItem.accuserShowInfo
    }
    return {data: data}
  },
  mounted() {
    window.layui.use("table",  () =>{
      window.layui.table.on('tool(questionTable)',  (obj) =>{
        console.log(obj)
        let witnessIndex=obj.data.witness_index
        let QAIndex=obj.data.QAindex
        this.data.accuser_evidence_witness[witnessIndex].witness_testimony[QAIndex].question=obj.data.question
        window.layer.close(window.layer.index)
        });

    })

  },
  computed: {

    // 
    getCrossExaminationName: {
      get() {
        let defendant = this.$store.state.defendant_item.map(e => (e.defendant_short == '' ? e.defendant : e.defendant_short) + "").filter(i => i && i != "")
        let accuser = this.$store.state.plaintiff_item.map(e => (e.accuser_short == '' ? e.accuser : e.accuser_short) + "").filter(i => i && i != "")
        let thirdparty = this.$store.state.third_party_item.map(e => (e.third_party_short == '' ? e.third_party : e.third_party_short) + "").filter(i => i && i != "")
        return accuser.concat(defendant).concat(thirdparty)
      }
    },
    getAccuserName: {
      get() {
        return this.$store.state.plaintiff_item.map(e => e.accuser_short == '' ? e.accuser : e.accuser_short).filter(i => i && i.trim())
      }
    },
    //
    getProofPlaintiff: {
      get() {
        let option_label = this.data.accuser_evidence.filter(i => i.evidence).map(e => {
          return "" + this.numberToChinese(e.serial) + ":" + e.evidence;
        })
        // let optionn_label={evidence_serial:evidence_serial,name_serial:name_serial}

        return [{evidence: "", option_label: option_label}]
      },
    },
    getQuizzerName: {
      get() {
        let defendant = this.$store.state.defendant_item.map(e => (e.defendant_short == '' ? e.defendant : e.defendant_short) + "").filter(i => i && i != "")
        let accuser = this.$store.state.plaintiff_item.map(e => (e.accuser_short == '' ? e.accuser : e.accuser_short) + "").filter(i => i && i != "")
        let thirdparty = this.$store.state.third_party_item.map(e => (e.third_party_short == '' ? e.third_party : e.third_party_short) + "").filter(i => i && i != "")
        let judge = this.$store.state.judge_name.filter(i => i && i.trim()).map(e => e + "")
        return judge.concat(accuser).concat(defendant).concat(thirdparty)
      }
    },
    getResponderName: {
      get() {
        let defendant = this.$store.state.defendant_item.map(e => (e.defendant_short == '' ? e.defendant : e.defendant_short) + "").filter(i => i && i != "")
        let accuser = this.$store.state.plaintiff_item.map(e => (e.accuser_short == '' ? e.accuser : e.accuser_short) + "").filter(i => i && i != "")
        let thirdparty = this.$store.state.third_party_item.map(e => (e.third_party_short == '' ? e.third_party : e.third_party_short) + "").filter(i => i && i != "")
        return ["","",""].concat(accuser).concat(defendant).concat(thirdparty)
      }
    },
    setIsDefendantEvidence: {
      get() {
        return this.$store.state.is_defendant_evidence
      },
      set(value) {
        this.data.is_defendant_evidence = value
        this.$store.commit('setIsDefendantEvidence', value)
      }
    }
  },
  components: {
    VueMultiselect
  },
  methods: {
    select_question(witness_index,QAindex){
      window.layui.use('table', function () {
        var table = window.layui.table;

        let result=[
          {question:"///",witness_index:witness_index,QAindex:QAindex},
          {question:"//",witness_index:witness_index,QAindex:QAindex},
          {question:" / ",witness_index:witness_index,QAindex:QAindex},
          {question:"",witness_index:witness_index,QAindex:QAindex},
          {question:"",witness_index:witness_index,QAindex:QAindex},
          {question:"",witness_index:witness_index,QAindex:QAindex},
          {question:"",witness_index:witness_index,QAindex:QAindex},
          {question:" /  / ",witness_index:witness_index,QAindex:QAindex},
          {question:"",witness_index:witness_index,QAindex:QAindex},
          {question:" /  /  / ",witness_index:witness_index,QAindex:QAindex},
          {question:" /  /  / ",witness_index:witness_index,QAindex:QAindex},
          {question:" / ",witness_index:witness_index,QAindex:QAindex},
          {question:" / ",witness_index:witness_index,QAindex:QAindex},
          {question:" /  / ",witness_index:witness_index,QAindex:QAindex},
          {question:" /  / ",witness_index:witness_index,QAindex:QAindex}
        ]
        window.layer.open({
          type: 1,
          area: ['50%', '50%'],
          title: "",
          content: '<div><table id="questionTable" lay-filter="questionTable"></table></div>', //div
          success:  () =>{
            table.render({
              elem: '#questionTable',
              data: result,
              cols: [[
                {field: 'question', align:'center', sort: true, title: ''},
                {field: 'witness_index', align:'center',sort: true, title: '',hide:true},
                {field: 'QAindex', align:'center',sort: true, title: '',hide:true},
                {align: 'center', toolbar: '#questionBar', title: '',width: 150}
              ]]
            });
          }
        });
      })
    },
    add_component(datatype, index, QAindex) {
      switch (datatype) {
        case "accuser_evidence":
          //accuser_evidence 
          // this.data.accuser_evidence.push({
          //   serial: this.data.accuser_evidence.length+1,//
          //   evidence: "", //( )
          //   content: "", //( )
          //   evidence_type: ""//
          // });
          this.data.accuser_evidence.splice(index, 0, {
            serial: 0,//
            evidence: "", //( )
            content: "", //( )
            evidence_type: ""//
          })
          for (let i = 0; i < this.data.accuser_evidence.length; i++) {
            this.data.accuser_evidence[i].serial = i + 1
          }
          for (let i = 0; i < this.data.accuser_evidence_witness.length; i++) {
            this.data.accuser_evidence_witness[i].serial = this.data.accuser_evidence.length + i + 1;
          }

          break;
        case "defendant_and_other_accuser_query":
          //
          this.data.defendant_and_other_accuser_query.splice(index, 0, {
            name: [], //
            evidence: [], // 
            facticity: "1", //
            legality: "1", //
            relevance: "1", //
            fact_reason: "", //
          });
          break;
        case "accuser_evidence_witness":
          this.data.accuser_evidence_witness.splice(index, 0, {
            serial: 1,//
            evidence: "",
            witness_name: "",
            witness_type: "",
            content: "",
            witness_testimony: [{
              quizzer: "",
              question: "",
              responder: "",
              answer: ""
            }]
          });
          for (let i = 0; i < this.data.accuser_evidence_witness.length; i++) {
            this.data.accuser_evidence_witness[i].serial = this.data.accuser_evidence.length + i + 1;
          }
          break;
        case "witnessQA":
          this.data.accuser_evidence_witness[index].witness_testimony.splice(QAindex, 0, {
                quizzer: "",
                question: "",
                responder: "",
                answer: ""
              }
          )
          break;
        default:
          //
          break;
      }
    },
    delete_component(datatype, index, QAindex) {
      switch (datatype) {
        case "accuser_evidence":
          if (this.data.accuser_evidence.length < 2) {
            window.layer.msg("", {icon: 5, time: 1500});
            return;
          }
          //
          this.data.accuser_evidence.splice(index, 1);
          for (let i = 0; i < this.data.accuser_evidence.length; i++) {
            this.data.accuser_evidence[i].serial = i + 1
          }
          for (let i = 0; i < this.data.accuser_evidence_witness.length; i++) {
            this.data.accuser_evidence_witness[i].serial = this.data.accuser_evidence.length + i + 1;
          }
          break;
        case "defendant_and_other_accuser_query":
          if (this.data.defendant_and_other_accuser_query.length < 2) {
            window.layer.msg("", {icon: 5, time: 1500});
            return;
          }
          //
          this.data.defendant_and_other_accuser_query.splice(index, 1);
          break;
        case "accuser_evidence_witness":
          if (this.data.accuser_evidence_witness.length < 2) {
            window.layer.msg("", {icon: 5, time: 1500});
            return;
          }
          this.data.accuser_evidence_witness.splice(index, 1);
          for (let i = 0; i < this.data.accuser_evidence_witness.length; i++) {
            this.data.accuser_evidence_witness[i].serial = this.data.accuser_evidence.length + i + 1;
          }
          break;
        case "witnessQA":
          if (this.data.accuser_evidence_witness[index].witness_testimony.length < 2) {
            window.layer.msg("", {icon: 5, time: 1500});
            return;
          }
          this.data.accuser_evidence_witness[index].witness_testimony.splice(QAindex, 1,)
          break;
        default:
          //
          break;
      }
    },
    numberToChinese(num) {
      var AA = new Array("", "", "", "", "", "", "", "", "", "", "");
      var BB = new Array("", "", "", "", "", "", "", "");
      var a = ("" + num).replace(/(^0*)/g, "").split("."),
          k = 0,
          re = "";
      for (var i = a[0].length - 1; i >= 0; i--) {
        switch (k) {
          case 0:
            re = BB[7] + re;
            break;
          case 4:
            if (!new RegExp("0{4}//d{" + (a[0].length - i - 1) + "}$")
                .test(a[0]))
              re = BB[4] + re;
            break;
          case 8:
            re = BB[5] + re;
            BB[7] = BB[5];
            k = 0;
            break;
        }
        if (k % 4 == 2 && a[0].charAt(i + 2) != 0 && a[0].charAt(i + 1) == 0)
          re = AA[0] + re;
        if (a[0].charAt(i) != 0)
          re = AA[a[0].charAt(i)] + BB[k % 4] + re;
        k++;
      }
      if (a.length > 1) // ()
      {
        re += BB[6];
        for (i = 0; i < a[1].length; i++)
          re += AA[a[1].charAt(i)];
      }
      if (re == '')
        re = "";
      if (re.match(/^/) && re.length == 3)
        re = re.replace("", "");
      return re;
    }

  },
  watch: {
    data: {
      handler() {
        //
        if (this.$store.state.court_number == "") {
          // window.layui.layer.msg('');
        } else {
          var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
          if (wholeItem != null) {
            wholeItem.accuserShowInfo = this.data
            localStorage.setItem(this.$store.state.court_number, JSON.stringify(wholeItem))
          }
        }
      },
      deep: true
    },
    accuser_is_witness: {
      handler() {
        if (this.data.accuser_is_witness == '1') {
          for (let i = 0; i < this.data.accuser_evidence_witness.length; i++) {
            this.data.accuser_evidence_witness[i].serial = this.data.accuser_evidence.length + i + 1;
          }
        }
      },
      immediate: true
    }

  },
};
</script>



